import React, { useState, forwardRef } from "react";
import "../../App.css";
import "./About.css";

import { TypingEffect } from "../widgets";
import { useWindowDimensions } from "../../hooks";

const About = forwardRef((props, ref) => {
    const windowSize = useWindowDimensions();

    return (
        <div className="section about-bkg" style={{ minHeight: windowSize.height }} ref={ref}>
            <div className="introduction-container">
                <span className="h3 introduction-text" style={{ animationDelay: "1250ms" }}>Hello there!</span>
                <span className="h3 introduction-text" style={{ animationDelay: "2775ms" }}>My name is <span style={{ borderBottom: "4px solid white" }}>Dale Christian Seen</span>.</span>
                <span className="h3 introduction-text" style={{ animationDelay: "4300ms" }}>I'm an aspiring <span style={{ color: "#3498db" }}>front-end developer</span>.</span>
                <div className="introduction-border-anims" />
            </div>
            <div className="introduction-container">
                <span className="body1 introduction-text" style={{ animationDelay: "5825ms" }}>I'm still in the middle of building my web portfolio. Come back later!</span>
            </div>
        </div>
    );
});

export default About;

